body {
	min-width: 320px;
	background-color: $pale-grey;
	font-family: 'Nunito Sans', sans-serif;
	@include f(16px, 400, 1.875); }

.inner {
	position: relative;
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 15px;
	padding-right: 15px;
	@include clr;
	@include lg {
		max-width: 970px; }
	@include md {
		max-width: 750px; }
	@include sm {
		max-width: 100%; } }

.title {
	color: $white;
	@include f(35px, 300, 1.429);
	letter-spacing: 1px;
	strong {
		font-weight: 800;
		@include xs {
			display: block; } }
	@include lg {
		font-size: 30px; } }

