.menu {
	display: inline-block;
	vertical-align: top;
	font-size: 0;
	padding-top: 10px;
	@include sm {
		display: block;
		padding: 0 0 20px; }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 15px;
		@include sm {
			padding: 0;
			display: block; } }
	&__control {
		display: inline-block;
		color: $white;
		@include f(15px, null, 1.34);
		transition: color .3s linear;
		@include sm {
			font-size: 22px;
			line-height: 60px; }
		&:hover {
			color: $charcoal-grey; } } }
